.header {
  /* padding: 0px 0px; */
  width: 100vw;
  position: fixed;
  top: 0;left: 0;
  z-index: 5;
  display: flex;
  /* background: linear-gradient(#6616fc, #f394d3); */
  background-color: black;
  color: white;
  justify-content: space-between;
  align-items: center;
  height: 70px;
}
.logo {
  width: 45px;
  height: 45px;
}
.logo-nav {
  display: flex;
  align-items: center;
  justify-content: space-evenly;
}
.logo-container {
  display: flex;
  align-items: center;
  justify-content: center;
}

.nav-options {
  display: flex;
  width: 100%;
  height: 100vh;
  position: absolute;
  top:50px;
  left: -100%;
  opacity: 0;
  transition: all 0.5s ease;
  flex-direction: column;
  list-style-type: none;
  grid-gap: 0px;
}
.nav-options.active {
  /* background: #9899d1; */
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  background-color:whitesmoke;
  left: 0;
  opacity: 1;
  transition: all 0.5s ease;
  z-index: 1;
  align-content: center;
  padding-left: 0px;
}



.menu-icon {

  margin-right: 25px;
}
.option {
  display: flex;
  align-items: center;
  justify-content: center;
  /* height: 10vw; */
  /* padding: 30px 0px; */
}
